import { ViewModelContext, BindingData } from '@farris/mobile-devkit';

class EntityManipulationService {

  private get bindingData(): BindingData {
    return this.viewModelContext.bindingData;
  }

  constructor(private viewModelContext: ViewModelContext) {
  }

  /**
   * 设置path对应的字段值
   */
  public setPropValue(path: string[], value: any): any {

    // 实体层还没有封装，临时用BindingData相关方法代替
    this.bindingData.setValue(path, value, true, true);
  }

}

export { EntityManipulationService };
