
/**
 * 上传控件的返回结果（该结构应该放在UI层，临时放在这里）
 */
interface BusinessUploadFileInfo {
  metadataId: string;
  fileName: string;
}


/**
 * 附件信息定义
 */
interface BusinessAttachmentInfo {

  /**
   * 附件id
   */
  attachmentId: string;

  /**
   * 附件名称
   */
  fileName: string;
}


/**
 * 服务器端所需的附件信息定义
 */
interface BusinessServerAttachmentInfo {

  /**
   * 表名路径
   */
  NodeCodes: string[];

  /**
   * 主键路径
   */
  HiretryIds: string[];

  /**
   * 附件id数组（批量新增时使用）
   */
  AttachmentIds?: string[];

  /**
   * 附件id（更新时使用）
   */
  AttachmentId?: string;
}

export { BusinessUploadFileInfo, BusinessAttachmentInfo, BusinessServerAttachmentInfo };
