import { Observable, Subject } from 'rxjs';
import { IJsBridgeStrategy, PhotoOptions, PictureOptions, VidepOptions, LocationInfo } from './decorators';

class CCworkService implements IJsBridgeStrategy {

    init(callback) {
        window.top['iCityBridge'].init(callback)
    }

    /**
     * {
     * quality:图片质量，可选值high、mid、low, high为1920*1080；mid为720*540；low: 480*360，默认为mid
     * mode:唤起模式，可选值both（仅支持iOS）、camera、album, 默认为camera;
     * mark文字水印，字数最大值50
     * }
     * @param options Object
     *
     */
    takePhoto(options: PhotoOptions = { quality: 'mid' }): Observable<[]> {
        // callback return object {errormessage:""，result:"base64格式"}
        const subject = new Subject<any>();

        const callback = (data) => {
            if (data) {
                subject.next(data['result']);
            }
        }

        window.top['iCityBridge'].ccworkGetPicsFromCameraAndAlbum(
            { ...options },
            callback
        );
        return subject;
    }

    /**
     * path 如果不为空则从指定图片文件识别二维码,空就吊起相机
     * filters 条码类型过滤列表，如QR、EAN13、EAN8
     * 
     * @param options Object
     * 
     */
    scanQRCode(): Observable<string> {
        const subject = new Subject<any>();

        const callback = (data) => {
            if (data) {
                subject.next(data['result']);
            }
        }

        window.top['iCityBridge'].ccworkScanCode({}, callback);
        return subject;
    }

    getSysInfo() {

    }

    getLocation(): Observable<LocationInfo> {
        /**
         *  回调函数
         *  输出结构 object
         *  errormessage:""
         *  result:Object {
         *  addr:'山东省济南市历下区经十路靠近浪潮'
         *  city:"济南"
         *  country:"中国"
         *  course:-1
         *  district:"历下区"
         *  lat:36.66254123263889
         *  lng:117.12974745008681
         *  province:"山东省"
         *  street:"经十路"
         *  streetNum:"1单元"
         * }
         *  status:true
         */
        const subject = new Subject<any>();

        const callback = (data) => {
            if (data) {
                subject.next(data['result']);
            }
        }

        window.top['iCityBridge'].ccworkGetLocation(
            callback
        );
        return subject;
    }

    // TODO:云上协同选择文件
    selectFile(): Observable<[]> {
        const subject = new Subject<any>();
        subject.next([]);
        return subject;
    }


    /**
     * 
     * @param number string
     * @param content string
     */
    sendSMS(number: string, content: string): void {
        const options = {
            numbers: [number],
            content
        }
        window.top['iCityBridge'].ccworkSendSms(options)
    }

    openSendSMS(number: string, content: string): void {
        window.top['iCityBridge'].sendSMS({ tel: number, msg: content })

    }

    /**
     * 
     * @param number string
     */
    tel(number: string): void {
        window.top['iCityBridge'].ringUp(number)
    }

    /**
     * 
     * @param options 
     */
    selectPictureFromAlbum(options: PictureOptions = { maxNum: 9, mType: 0 }): Observable<[]> {
        /**
         * params object 
         *  { maxNum : number 9 最大可选数 默认9
         *    mType: number 0 	选择模式，可选值0/1, 0:图片，1：视频;
         * }
         */
        const subject = new Subject<any>();

        const callback = (data) => {
            if (data) {
                subject.next(data['result']);
            }
        }

        window.top['iCityBridge'].ccworkGetPicsFromAlbum(
            options,
            callback
        );
        return subject;
        /**
         *  object
         *  errormessage:""
         *  result:Array [
         * base64
         * ]
         *  status:true
         */
    }

    /**
     * 
     * @param options  
     */
    takeVideo(options: VidepOptions): Observable<string> {
        /**
         * params object 
         *  { 
         *  time : number 短视频时间（秒） 最大60s
         *  quality: number 0 	视频质量 取值范围0~1
         *  id :string 	视频id（视频名字）
         * }
         */
        const subject = new Subject<any>();

        const callback = (data) => {
            if (data) {
                subject.next(data['result']);
            }
        }

        window.top['iCityBridge'].ccworkTakeShortVideo(
            {
                time: 15,
                ...options
            }
            ,
            callback
        );
        return subject;
        /**
         *  object
         *  errormessage:""
         *  result:Object {
         *  value:'path'
         * }
         *  status:true
         */
    }

    closeWindow(): void {
        window.top['iCityBridge'].closeWebView()
    }

    /**
     * 
     * @param title string
     */
    setTitles(title: string): void {
        window.top['iCityBridge'].setTitle(title)
    }

    downloadFile(options) {
        alert('暂不支持附件下载');
    }

    setOriginGoback(fn?) {
        window.top['iCityBridge'].ccworkInterceptOriginGoback(!!fn, fn, () => { })
    }

    pushOriginGoback(fn?: any) {
        window['OriginGoback'] = window['OriginGoback'] || [];
        const OriginGoback = () => {
            const gobackFn = window['OriginGoback'].pop()
            if (gobackFn) {
                gobackFn()
            }
            else {
                window.top['iCityBridge'].ccworkInterceptOriginGoback(false, () => { }, () => { })
                history.back();
            }
        }
        if (fn) {
            window['OriginGoback'].push(fn)
            window.top['iCityBridge'].ccworkInterceptOriginGoback(true, OriginGoback, () => { })
        }
    }
    popOriginGoback(fn?: any) {
        window['OriginGoback'].pop()
    }

    playShortVideo(opt: any) {
        window.top['iCityBridge'].ccworkPlayShortVideo(opt);
    }


}

export { CCworkService }