import { Observable, of, EMPTY } from 'rxjs';
import { switchMap, tap } from 'rxjs/operators';
import { ViewModelContext } from '@farris/mobile-devkit';
import { LoadingService, NotifyService } from '../ui-services/index';
import { WfTaskHandlerService } from '../work-flow-service/wf-task-handler.service'
class ApproveService {

    private wfTaskHandlerService: WfTaskHandlerService;

    private notifyService: NotifyService;

    constructor(wfTaskHandlerService: WfTaskHandlerService, protected viewModelContext: ViewModelContext) {
        this.wfTaskHandlerService = wfTaskHandlerService;
        this.notifyService = this.getService<NotifyService>(NotifyService);
    }

    /**
     * 获取服务实例
     */
    getService<T>(token: any): T {
        const injector = this.viewModelContext.injector;
        return injector.get<T>(token);
    }

    /**
     * 提交审批(带入口单据使用wf控件)
     * @param bizBillID 业务单据Id
     * @param bizDefKey 入口单据Id
     * @param options 上下文参数
     * @param variables 可选参数
     */
    public submitApproveWithBizDefKeyUseControl(bizBillID: string, bizDefKey: string, options: any = {}, variables?: any) {
        if (!bizBillID) {
            this.notifyService.error('业务单据Id不能为空');
            return EMPTY;
        }
        if (!bizDefKey) {
            this.notifyService.error('入口单据Id不能为空');
            return EMPTY;
        }
        if (!options || typeof options !== 'object') {
            options = {};
        }
        const payload: any = {
            dataId: bizBillID,
            bizDefKey,
            ...options
        };
        // 处理variables参数
        if (variables) {
            if (variables.startsWith('{') && variables.endsWith('}')) {
                try {
                    variables = JSON.parse(variables);
                } catch {
                    console.log('variables parse failed!');
                    variables = {};
                }
            }
            payload.variables = variables;
        }
        return this.wfTaskHandlerService && this.wfTaskHandlerService.startProcess(payload);
    }

    /**
     * 取消审批(支持主表、子表)
     * 迁移web端（已废弃）请调用新的取消提交方法
     * @param procInstId 流程实例Id
     */
    public cancelSubmit(procInstId: string): Observable<any> {
        if (!procInstId) {
            this.notifyService.error('流程实例Id不能为空');
            return EMPTY;
        }
        return this.wfTaskHandlerService && this.wfTaskHandlerService.cancelSubmit({ dataId: null, bizDefKey: null, procInstId });
    }

    /**
     * 提交审批
     * @param dataId 表单id
     * @param bizDefKey 入口单据id
     */
    public submitWithBizDefKey(dataId: string, bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.submitWithBizDefKey(dataId, bizDefKey);
    }

    /**
     * 取消提交审批
     * @param dataId 表单id
     * @param bizDefKey 入口单据id
     */
    public cancelSubmitWithDataId(dataId: string, bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.cancelSubmitWithDataId(dataId, bizDefKey);
    }

    /**
     * 批量提交审批
     * @param dataIds 表单ID集合
     * @param bizDefKey 入口单据id
     */
    public batchSubmitWithBizDefKey(dataIds: string[], bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.batchSubmitWithBizDefKey(dataIds, bizDefKey);
    }

    /**
     * 批量取消提交审批
     * @param dataIds 表单ID集合
     * @param bizDefKey 入口单据id
     */
    batchCancelSubmitWithDataId(dataIds: string[], bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.batchCancelSubmitWithDataId(dataIds, bizDefKey);
    }

    /**
    * 子表提交审批
    * @param parentDataId 主表id
    * @param childDataId 子表id
    * @param bizDefKey 入口单据id
    */
    childSubmit(parentDataId: string, childDataId: string, bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.childSubmit(parentDataId, childDataId, bizDefKey);
    }

    /**
     * 子表取消提交审批
     * @param parentDataId 主表id
     * @param childDataId 子表id
     * @param bizDefKey 入口单据id
     */
    childCancelSubmit(parentDataId: string, childDataId: string, bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.childCancelSubmit(parentDataId, childDataId, bizDefKey);
    }

    /**
     * 子表批量提交审批
     * @param parentDataId 主表id
     * @param childDataIds 子表id集合
     * @param bizDefKey 入口单据id
     */
    childBatchSubmit(parentDataId: string, childDataIds: string[], bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.childBatchSubmit(parentDataId, childDataIds, bizDefKey);
    }

    /**
     * 子表批量取消提交审批
     * @param parentDataId 主表id
     * @param childDataIds 子表id集合
     * @param bizDefKey 入口单据id
     */
    childBatchCancelSubmit(parentDataId: string, childDataIds: string[], bizDefKey: string): Observable<any> {
        return this.wfTaskHandlerService && this.wfTaskHandlerService.childBatchCancelSubmit(parentDataId, childDataIds, bizDefKey);
    }

}

export { ApproveService }