import { Observable, EMPTY, forkJoin } from 'rxjs';
import { tap } from 'rxjs/operators';

import { ViewModelContext, CommandContext, BindingPathConverter } from '@farris/mobile-devkit';
import { UploadFileInfo } from './types';
import { AttachmentUtil } from './attachment.util';
import { AttachmentDataService } from './attachment-data.service';
import { EntityService } from '../entity-services/index';
import { RemoveDataService } from '../data-services/remove-data.service';


/**
 * 附件服务
 */
class AttachmentService {

  /**
   * 默认根目录
   */
  private defaultRootDirId = 'default-root';

  /**
   * 默认子目录
   */
  private get defaultParentDirName(): string {
    return this.viewModelContext.bindingData.list.currentId;
  }

  /**
   * 视图模型
   */
  private viewModelContext: ViewModelContext;

  /**
   * 附件数据服务
   */
  private attachDataService: AttachmentDataService;

  /**
   * 实体服务
   */
  private entityService: EntityService;

  /**
   * 实体服务
   */
  private removeDataService: RemoveDataService;

  /**
   * 构造函数
   */
  constructor(
    viewModelContext: ViewModelContext, entityService: EntityService,
    removeDataService: RemoveDataService, attachDataService: AttachmentDataService
  ) {

    this.viewModelContext = viewModelContext;
    this.attachDataService = attachDataService;
    this.entityService = entityService;
    this.removeDataService = removeDataService;
  }

  /**
   * 上传单个文件
   * @param attachmentIdPath 附件内码字段的路径，形如/attachInfo/attachmentId；
   * @param attachmentNamePath 附件名称字段的路径
   */
  public uploadAndUpdateRow(attachmentInfoFieldPath: string, rootDirId?: string, parentDirName?: string): Observable<any> {
    const rootId = rootDirId ? rootDirId : this.defaultRootDirId;
    const formId = parentDirName ? parentDirName : this.defaultParentDirName;
    if (!rootId || !formId) {
      throw new Error('rootDirId和parentDirName不能为空，请填写');
    }

    const fileInfos = this.getUploadFileInfosFromContext();
    const attachmentInfos = AttachmentUtil.convertToAttachmentInfos(fileInfos);
    if (!attachmentInfos || attachmentInfos.length === 0) {
      alert('请先上传附件');
      return EMPTY;
    }

    // 更新服务器端
    console.log('show loading ...');
    const firstAttachmentInfo = AttachmentUtil.getFirstAttachmentInfo(attachmentInfos);
    const result$ = this.attachDataService.updateRow(attachmentInfoFieldPath, firstAttachmentInfo).pipe(
      tap(() => {
        console.log('hide loading ...');
      })
    );
    return result$;
  }

  /**
   * 上传多个文件
   */
  public uploadAndBatchAddRows(attachmentInfoFieldPath: string, rootDirId?: string, parentDirName?: string): Observable<any> {
    const rootId = rootDirId ? rootDirId : this.defaultRootDirId;
    const formId = parentDirName ? parentDirName : this.defaultParentDirName;
    if (!rootId || !formId) {
      throw new Error('rootDirId和parentDirName不能为空，请填写');
    }

    const fileInfos = this.getUploadFileInfosFromContext();
    const attachmentInfos = AttachmentUtil.convertToAttachmentInfos(fileInfos);
    if (!attachmentInfos || attachmentInfos.length === 0) {
      alert('请先上传附件');
      return EMPTY;
    }

    // 更新服务器端
    console.log('show loading ...');
    const result$ = this.attachDataService.updateRows(attachmentInfoFieldPath, attachmentInfos).pipe(
      tap(() => {
        console.log('hide loading ...');
      })
    );

    return result$;
  }

  /**
   * 批量删除附件所在的行
   */
  public removeAttachmentRows(attachmentInfoFieldPath: string): Observable<any> {
    if (!attachmentInfoFieldPath) {
      return EMPTY;
    }
    const dataIds = this.getDataIdsToRemove(attachmentInfoFieldPath);
    //如果是主表
    if (attachmentInfoFieldPath.split('/').length <= 2) {
      return this.removeDataService.removeByIds(dataIds);
    }
    // 如果是子表
    else {
      const removeObservables: Observable<any>[] = [];
      if (dataIds.length === 0) {
        alert('请选择要删除的文件');
      }
      const bindingListPath = this.getBindingListPathWithAttachments(attachmentInfoFieldPath);
      dataIds.forEach((dataId: string) => {
        const removeObservable = this.removeDataService.removeByPathAndId(bindingListPath, dataId);
        removeObservables.push(removeObservable);
      });
      return forkJoin(removeObservables);
    }
  }

  // #region 工具方法
  /**
   * 从上下文中中获取控件传递的附件信息
   */
  private getUploadFileInfosFromContext(): UploadFileInfo[] {
    const commandContext = this['context'] as CommandContext;
    const uploadFileInfos = commandContext.eventParams as UploadFileInfo[];
    if (!uploadFileInfos) {
      return [];
    }
    return uploadFileInfos;
  }

  /**
   * 获取要删除的附件对应的数据id数组
   */
  public getDataIdsToRemove(attachmentInfoFieldPath: string) {
    const attachIds = this.getAttachmentIdsToRemoveFromContext();
    const dataIds: string[] = [];
    attachIds.forEach((attachId: string) => {

      // 上传删除和预览删除传递过来的fileId的key可能不一致，要做兼容
      const dataId = this.convertAttachmentIdToDataId(attachId, attachmentInfoFieldPath);
      dataIds.push(dataId);
    });
    return dataIds;
  }

  /**
   * 从命令上下文中获取要删除附件ids
   */
  public getAttachmentIdsToRemoveFromContext() {
    const commandContext = this['context'] as CommandContext;
    return commandContext.eventParams as string[];
  }

  /**
   * 根据路径获取附件字段值数组
   * @param fieldPath 字段路径
   */
  private convertAttachmentIdToDataId(fileId: string, attachmentInfoFieldPath: string): string {

    // 解析路径
    const attachInfoBindingPath = BindingPathConverter.toBindingPathArray(attachmentInfoFieldPath);
    const attachFieldName = attachInfoBindingPath.pop();
    const attachListBindingPath = attachInfoBindingPath;

    // 获取附件id数组
    const entityListData = this.entityService.getEntityListData(attachListBindingPath);
    const targetEntityData = entityListData.find((entityData: any) => {
      if (entityData[attachFieldName]) {
        const attachmentId = entityData[attachFieldName]['attachmentId'];
        if (attachmentId === fileId) {
          return true;
        }
      }
    });

    return targetEntityData.id;
  }

  /**
   * 获取带附件的BindingList的Path
   */
  private getBindingListPathWithAttachments(attachmentInfoFieldPath: string) {
    const attachInfoBindingPath = BindingPathConverter.toBindingPathArray(attachmentInfoFieldPath);
    attachInfoBindingPath.pop();
    const bindingListPath = attachInfoBindingPath;
    return '/' + bindingListPath.join('/');
  }

  // #endregion

}

export { AttachmentService };
