import { Observable } from "rxjs";

interface IJsBridgeStrategy {


    // 拍照
    takePhoto(options: PhotoOptions): Observable<[]>

    // 二维码扫描
    scanQRCode(): Observable<string>

    // 获取定位信息
    getLocation(): Observable<LocationInfo>

    // 发送短信
    sendSMS(number: string, content: string): void

    // 打开发送短信页面
    openSendSMS(number: string, content: string): void

    // 打开拨打电话界面
    tel(number: string): void

    // 获取图片
    selectPictureFromAlbum(options: PictureOptions): Observable<[]>

    // 拍摄视频
    takeVideo(options: VidepOptions): Observable<string>

    // 关闭窗口
    closeWindow(): void

    // 更改标题
    setTitles(title: string): void

    // 选择文件
    selectFile(options?: any): any

    // 下载文件
    downloadFile(options: any): any;

    // 获取设备信息
    getSysInfo(): any;

    // 监听返回
    setOriginGoback(fn?: any): any

    // 添加全面屏手势返回方法
    pushOriginGoback(fn?: any): any

    // 删除全面屏手势返回方法
    popOriginGoback(fn?: any): any;

    // 播放短视频
    playShortVideo(options?: any): any;
}

// 拍照参数
interface PhotoOptions {
    // 照片质量 云加 1-100 云上协同 high、mid、low 默认为mid
    quality: string | number,
    encodingType?: any,
    targetWidth?: number,
    targetHeight?: number,
    waterMark?: WaterMark
}

// 水印
interface WaterMark {
    content: string,
    color: string,
    background: string,
    algin: string,
    valign: string
}

// 选择图片参数
interface PictureOptions {
    maxNum: number,  // 最大可选数 默认9
    mType: number, // 选择模式，可选值0/1, 0:图片，1：视频;
    quality?: string | number  // 照片质量 云加 1-100 云上协同 high、mid、low 默认为mid
}

interface VidepOptions {
    id: string; //视频id（视频名字
    quality?: number; // 视频质量 取值范围0~1
}

interface LocationInfo {
    addr: string,
    city: string
    country: string
    course?: number
    district: string
    lat: number
    lng: number
    province: string
    street: string
    streetNum: string
}

export { IJsBridgeStrategy, PhotoOptions, PictureOptions, VidepOptions, LocationInfo }