import { ViewModelContext, BindingData, BindingList } from '@farris/mobile-devkit';
import { EntityTraversingService } from './entity-traversing.service';

interface SplitedPath {
  listPath: string[];
  propPath: string[];
}

/**
 * 实体聚合服务
 */
class EntityAggregationService {

  private get bindingData(): BindingData {
    return this.viewModelContext.bindingData;
  }

  private traversingService: EntityTraversingService;

  constructor(private viewModelContext: ViewModelContext) {
    this.traversingService   = this.viewModelContext.injector.get<EntityTraversingService>(EntityTraversingService);
  }

  /**
   * 获取path对应列表记录的条数
   */
  public count(path: string[]): number {
    const splitedPath = this.splitPath(path);
    const entityListData = this.traversingService.getEntityListData(splitedPath.listPath);
    return entityListData.length;
  }

  /**
   * 获取path对应字段的和
   */
  public sum(path: string[]) {
    const splitedPath = this.splitPath(path);
    const entityListData = this.traversingService.getEntityListData(splitedPath.listPath);

    const total = entityListData.reduce((accTotal: number, entityData: any) => {
      let propValue = parseFloat(this.getPropValue(entityData, splitedPath.propPath));
      propValue = isNaN(propValue) ? 0 : propValue;
      accTotal += propValue;
      return accTotal;
    }, 0);
    return total;
  }

  /**
   * 获取path对应字段的平均值
   */
  public avg(path: string[]) {
    const count = this.count(path);
    const total = this.sum(path);
    return count !== 0 ? total / count : 0;
  }

  /**
   * 获取path对应字段的最大值
   */
  public max(path: string[]): number {
    const splitedPath = this.splitPath(path);
    const entityListData = this.traversingService.getEntityListData(splitedPath.listPath);

    const max =  entityListData.reduce((accMax: number, entityData: any) => {
      const propValue = parseFloat(this.getPropValue(entityData, splitedPath.propPath));
      if (isNaN(propValue) === false && ( !accMax || propValue > accMax)) {
        accMax = propValue;
      }
      return accMax;
    }, null);
    return max ? max : 0;
  }

  /**
   * 获取path对应字段的最小值
   */
  public min(path: string[]): number {
    const splitedPath = this.splitPath(path);
    const entityListData = this.traversingService.getEntityListData(splitedPath.listPath);

    const min = entityListData.reduce((accMin: number, entityData: any) => {
      const propValue = parseFloat(this.getPropValue(entityData, splitedPath.propPath));
      if (isNaN(propValue) === false && ( !accMin || propValue < accMin)) {
        accMin = propValue;
      }
      return accMin;
    }, null);
    return min ? min : 0;
  }

  /**
   * 获取属性值
   */
  private getPropValue(entityData: any, path: string[]) {
    let currentData = entityData;
    path.forEach((pathItem: string) => {

      // 提前退出的都重置为null，主要针对关联对象未赋值场景
      if (!currentData) {
        currentData = null;
        return;
      }
      currentData = currentData[pathItem];
    });

    return currentData;
  }

  /**
   * 分隔路径，分隔符列表路径和属性路径
   * @return 形如 {parentListPath: [], propertyPath: []}
   */
  private splitPath(path: string[]): SplitedPath {
    const listPath = path.concat([]);
    const propPath = [];
    let currentNode = this.bindingData.getValue(listPath);
    while (currentNode instanceof BindingList !== true) {
      const propPathItem = listPath.pop();
      if (!propPathItem) {
        return;
      }
      propPath.unshift(propPathItem);
      currentNode = this.bindingData.getValue(listPath);
    }

    return {
      listPath: listPath,
      propPath: propPath
    };
  }

}

export { EntityAggregationService };
