import { Notify } from '@farris/mobile-ui';
import { Observable, of, EMPTY } from 'rxjs';

/**
 * 消息提示服务
 * @summary
 * 页面边缘滑出，自动消失，语气略强于Toast
 */
export class NotifyService {

    /**
     * 消息提示
     */
    info(message: string, options?: any): void {
        (Notify as any).info(message, { duration : 1500, ...options });
    }

    /**
     * 成功提示
     */
    success(message: string, options?: any): void {
      (Notify as any).success(message, { duration : 1000, ...options });
    }

    /**
     * 警告提示
     */
    warning(message: string, options?: any): void {
      (Notify as any).warning(message, { duration : 1500, ...options });
    }

    /**
     * 失败提示
     */
    error(message: string, options?: any): void {
      (Notify as any).error(message, { duration : 1500, ...options });
    }

    /**
     * 自定义提醒
     */
    show(options: any) {
      (Notify as any).show(options);
    }
}
